ul {
  list-style-type: none;
}

.bi {
  color: white;
  scale: 1.3;
}

.form {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-content: flex-start;
  padding-top: 32px;
  padding-bottom: 48px;
}
.form__heading {
  display: flex;
  flex-direction: column;
}
.form__body {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.product-list__content {
  display: flex;
  gap: 4rem;
}
.product-list__sort {
  padding-bottom: 18px;
}

.filter {
  background-color: white;
  padding: 20px;
  border-radius: 16px;
}
.filter__content {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}
.filter__content label {
  display: flex;
  gap: 8px;
}
.filter__block {
  display: flex;
  flex-direction: column;
  gap: 8px;
}
.filter__block-content {
  display: flex;
  flex-direction: column;
  gap: 6px;
}

.applied-filter {
  display: flex;
  justify-content: start;
  gap: 18px;
  flex-wrap: wrap;
  margin-bottom: 16px;
}
.applied-filter__item {
  background-color: white;
  padding: 8px;
  border-radius: 16px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.4);
}

.col {
  min-width: 250px;
}/*# sourceMappingURL=styles.css.map */